"use strict";
Object.defineProperty(exports, "__esModule", { value: true });
var player_1 = require("./player");
var level_1 = require("./level");
window.onload = function () {
    var canvas = document.getElementById('gameCanvas');
    var splashScreen = document.getElementById('splashScreen');
    var playButton = document.getElementById('playButton');
    var musicToggleButton = document.getElementById('musicToggleButton');
    var musicIcon = document.getElementById('musicIcon');
    var scoreDisplay = document.getElementById('score');
    var splashMusic = document.getElementById('splashMusic');
    var gameMusic = document.getElementById('gameMusic');
    var collectSoundSrc = 'coin.wav';
    var leftButton = document.getElementById('leftButton');
    var rightButton = document.getElementById('rightButton');
    var jumpButton = document.getElementById('jumpButton');
    var context = canvas.getContext('2d');
    var isMusicPlaying = false;
    var currentScreen = 'splash'; // Possible values: 'splash', 'game'
    var screenOffset = 0;
    splashMusic.play();
    playButton.addEventListener('click', function () {
        splashScreen.style.display = 'none';
        canvas.style.display = 'block';
        scoreDisplay.style.display = 'block';
        leftButton.style.display = 'flex';
        rightButton.style.display = 'flex';
        jumpButton.style.display = 'flex';
        splashMusic.pause();
        currentScreen = 'game';
        if (isMusicPlaying) {
            gameMusic.play();
        }
        startGame();
    });
    musicToggleButton.addEventListener('click', function () {
        if (currentScreen === 'splash') {
            if (isMusicPlaying) {
                splashMusic.pause();
                musicIcon.classList.remove('fa-volume-up');
                musicIcon.classList.add('fa-volume-mute');
            }
            else {
                splashMusic.play();
                musicIcon.classList.remove('fa-volume-mute');
                musicIcon.classList.add('fa-volume-up');
            }
        }
        else if (currentScreen === 'game') {
            if (isMusicPlaying) {
                gameMusic.pause();
                musicIcon.classList.remove('fa-volume-up');
                musicIcon.classList.add('fa-volume-mute');
            }
            else {
                gameMusic.play();
                musicIcon.classList.remove('fa-volume-mute');
                musicIcon.classList.add('fa-volume-up');
            }
        }
        isMusicPlaying = !isMusicPlaying;
    });
    var startGame = function () {
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;
        var player = new player_1.Player();
        var level = new level_1.Level();
        var score = 0;
        var isMovingLeft = false;
        var isMovingRight = false;
        var gameLoop = function () {
            context.clearRect(0, 0, canvas.width, canvas.height);
            var maxScreenOffset = level.endMarkerX - window.innerWidth;
            // Adjust offset based on movement direction
            if (isMovingRight && screenOffset < maxScreenOffset) {
                screenOffset += player.speed;
                if (screenOffset > maxScreenOffset) {
                    screenOffset = maxScreenOffset;
                }
            }
            else if (isMovingLeft && screenOffset > 0) {
                screenOffset -= player.speed;
                if (screenOffset < 0) {
                    screenOffset = 0;
                }
            }
            // Update the player's position
            player.update(level.levelWidth);
            // Ensure the player does not go back behind position 0
            if (player.x < screenOffset) {
                player.x = screenOffset;
            }
            // Ensure the player does not go beyond the right edge of the screen
            if (player.x > screenOffset + window.innerWidth - player.width) {
                player.x = screenOffset + window.innerWidth - player.width;
            }
            level.update(player, screenOffset);
            // Pass screenOffset to level.draw
            level.draw(context, screenOffset);
            player.draw(context);
            // Update and draw score
            scoreDisplay.textContent = score.toString().padStart(3, '0');
            requestAnimationFrame(gameLoop);
        };
        // Touch controls for canvas
        canvas.addEventListener('touchstart', function (e) {
            var touch = e.touches[0];
            if (touch.clientX < canvas.width / 2) {
                isMovingLeft = true;
                player.moveLeft();
            }
            else {
                isMovingRight = true;
                player.moveRight();
            }
        });
        canvas.addEventListener('touchend', function () {
            isMovingLeft = false;
            isMovingRight = false;
            player.stop();
        });
        // Button controls
        leftButton.addEventListener('touchstart', function () {
            isMovingLeft = true;
            player.moveLeft();
        });
        leftButton.addEventListener('touchend', function () {
            isMovingLeft = false;
            player.stop();
        });
        rightButton.addEventListener('touchstart', function () {
            isMovingRight = true;
            player.moveRight();
        });
        rightButton.addEventListener('touchend', function () {
            isMovingRight = false;
            player.stop();
        });
        jumpButton.addEventListener('touchstart', function () {
            player.jump();
        });
        // Listen for item collection
        window.addEventListener('itemCollected', function () {
            score += 1;
            var collectSound = new Audio(collectSoundSrc);
            collectSound.play();
        });
        gameLoop();
    };
};
